import { Plane, Sphere } from "@react-three/drei";
import React, { useRef } from "react";
import { gsap } from "gsap";
import { useGSAP } from "@gsap/react";
gsap.registerPlugin(useGSAP);
const Background = () => {
  const backgroundRef = useRef();
  useGSAP(() => {
    gsap.from(backgroundRef.current.position, {
      duration: 5,
      ease: "expo.out",
      y: 100,
    });
  });
  return (
    <>
      <group ref={backgroundRef}>
        <Plane args={[50, 50, 20, 20]} position={[0, 0, -6]}>
          <meshStandardMaterial
            color={"blue"}
            wireframe={true}
            transparent={true}
          />
        </Plane>

        <Sphere position={[-3, 0, 0]} args={[10, 8, 16]}>
          <meshStandardMaterial
            color="blue"
            transparent={true}
            wireframe={true}
          />
        </Sphere>
      </group>
    </>
  );
};

export default Background;
