import React, { useRef, useEffect } from "react";
// import { useProgress, Html } from "@react-three/drei";
import styles from "./Loader.module.css";

import { progressAnimation, progressAnimationHide } from "./animations";

const Loader = ({ timeline, setLoaderFinished }) => {
  const progressRef = useRef();
  // const canvasContainerRef = useRef();

  useEffect(() => {
    if (timeline) {
      timeline.add(progressAnimation(progressRef));
      timeline.add(progressAnimationHide(progressRef));
      timeline.play().eventCallback("onComplete", () => {
        console.log("onComplete");
        setLoaderFinished(true);
        timeline.kill();
        // timeline
        //   .add(backgrounpFadeOutAnimation(progressRef))
        //   .play()
        //   .eventCallback("onComplete", () => {
        //     timeline.kill();
        //   });
      });
    }
  }, [timeline, setLoaderFinished]);

  return (
    <>
      {/* <Html center>cargando el zine, ya vamos por {progress} % </Html> */}
      <div className={styles.wrapper}>
        <div className={styles.progressWrapper}>
          <div className={styles.progress} ref={progressRef}></div>
        </div>
      </div>
    </>
  );
};

export default Loader;
