import gsap from "gsap";
export const progressAnimation = (progressRef) => {
  return gsap.to(progressRef.current, {
    scale: 15,
    duration: 2.5,
    ease: "power3.inOut",
  });
};

export const progressAnimationHide = (progressRef) => {
  return gsap.to(progressRef.current, {
    scale: 0,
    duration: 2.1,
    ease: "power3.inOut",
  });
};
